.header {
  display: flex;
  justify-content: space-between;
  min-height: 10vh;
  font-size: 2vh;
  font-weight: bold;
  border: 3px solid #555555;
  border-style: ridge;
  border-bottom: 0px;
  box-shadow: inset 2px 2px 21px 4px rgba(0, 0, 0, 0.6);
}

.header ul {
  display: flex;
  text-transform: uppercase;
  padding-top: 6vh;
}

.title {
  padding-top: 6vh;
  margin-left: 2vh;
  color: rgb(30, 113, 118);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 3vh;
  text-shadow: 3px 3px #071514;
}
